<template>
  <div class="shopbag">
    <div class="nav">
      <van-nav-bar
        class="nav_item"
        title="购物袋"
        left-text="返回"
        left-arrow
        fixed
        @click-left="goback"
        @click-right="isEdit = !isEdit"
      >
      <template #right>
        <div v-if="shopbagData.length > 0">
       {{isEdit ? '完成' : '编辑'}}
        </div>

      </template>
      </van-nav-bar>
    </div>

    <div class="content">
      <!-- 购物袋为空时 -->
      <div v-if="shopbagData == 0">
        <van-empty description="购物袋空着呢">
          <van-button round color="#780d00" class="bottom-button" @click="goShop">
            去逛逛
          </van-button>
        </van-empty>
      </div>
      <!-- 购物袋有内容时 -->
      <div v-else class="content_box">
        <div class="bgc"></div>

         <!-- 懒加载组件 -->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="哎呀，见底啦!"
        offset="20"
        @load="loadData"
      >



        <div class="content_item" v-for="(item,index) in shopbagData" :key="item.sid">
              <van-swipe-cell :disabled="!isEdit">
               <van-cell class="clearfix">
                 <div class="left fl">
                   <van-checkbox v-model="item.isChecked" @change="simpleSelect"></van-checkbox>
                 </div>
                 <div class="right fl">
                  <div class="img fl">
                      <img :src="item.small_img" alt="" class="auto-img">

                  </div>
                  <div class="text fl">
                  <div class="info">
                      <div class="name">{{item.name}}</div>
                      <div class="rule">{{item.rule}}</div>
                  </div>
                  <div class="eglname">{{item.enname}}</div>
                  <div class="clearfix">
                      <div class="price fl">￥{{item.price}}</div>
                      <div class="fr">
                         <van-stepper v-model="item.count" theme="round" button-size="22" disable-input  @change="changeCount(item)"/> 
                      </div>
                  </div>
                  </div>
                 </div>
               </van-cell>
               <template #right>
                 <van-button square type="danger" text="删除" @click="simpleMove(index)" class="del_button"/>
               </template>
             </van-swipe-cell> 
         </div>
      </van-list>

            <van-submit-bar v-if="!isEdit" :price="total" button-text="提交订单" class="checkbox" button-color="#780d00" @submit="commit">
              <van-checkbox v-model="isAllchecked" @click="allSelect">全选</van-checkbox>       
            </van-submit-bar>

            <van-submit-bar v-else button-text="删除选择" class="checkbox" @submit="selectMove" button-color="#780d00">
              <van-checkbox v-model="isAllchecked" @click="allSelect">全选</van-checkbox>       
            </van-submit-bar>

      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/shopbag.less";
export default {
  name: "Shopbag",
  data() {
    return {
      isEmpty: false,
      isEdit: false,
      isAllchecked:false,
      // 购物袋数据
      shopbagData:{},
      // 触发加载
      loading:true,

      // 是否加载完成
      finished:false,
      // 所有购物袋数据
      allShopbagData:[],

      // 每次触底懒加载截取8条数据
      dataCount:8,
      // 开始截取购物袋数据位置
      startIndex:0,
      total:0

    };
  },
  created() {
      this.getShopbagData()
  },
  methods: {
    goback() {
      this.$router.go(-1);
    },

    // 获取购物袋的数据
    getShopbagData(){
        // 获取tokenString
        let tokenString = localStorage.getItem('tokenString');
        if(!tokenString){
            this.$toast('请先登录');
            return this.$router.push({name:'Login'});
        }
        this.$toast.loading({
            message:"加载中...",
            forblidClick:true,
            duration:0
        });

        // 发起购物袋数据请求
        this.axios({
            methods:'GET',
            url:'/findAllShopcart',
            params:{
                appkey:this.appkey,
                tokenString
            }
        }).then(result =>{
            this.$toast.clear();
            // console.log(result);
            if(result.data.code == 700){
              this.$toast("请先登录");
              this.$router.push({name:'Login'})
            }else if(result.data.code == 5000) {

            result.data.result.map(v =>{
              v.isChecked = false;
            })
            this.allShopbagData = result.data.result;
            this.shopbagData = this.allShopbagData.slice(this.startIndex,this.startIndex + this.dataCount)
            // console.log(this.shopbagData);
            this.startIndex += this.dataCount;
            this.loading = false;

            }
            }).catch(err =>{
            this.$toast.clear();
            console.log(err);
        })
    },
    // 懒加载购物袋数据
    loadData(){

     setTimeout(() =>{

       let data = this.allShopbagData.slice(this.startIndex,this.startIndex+this.dataCount);
 
       this.startIndex += this.dataCount;
 
       this.shopbagData.push(...data);
 
       if(data.length < this.dataCount){
        //  没有数据可加载时，需要finished修改为true，这样可以不再onload事件
         this.finished = true;

       }else{
        //  每次触发onload事件都需要将loading修改为false
         this.loading = false;

       }
     },2000)

    },

  //  全选按钮
    allSelect(){
      this.shopbagData.map(v =>{

        v.isChecked = this.isAllchecked
      })
    },

// 单选按钮
    simpleSelect(){
      this.amount()
      for(let i=0;i<this.shopbagData.length;i++){
        if(!this.shopbagData[i].isChecked){
            this.isAllchecked = false;
            return;
        }
      }
      this.isAllchecked = true
    },
    // 单个移除商品
    simpleMove(index){

      this.shopbagData.splice(index,1);
      this.amount()
    },
    // 选择删除商品
    selectMove(){

     let bool = false;
      for(let i= 0;i<this.shopbagData.length;i++){
        if(this.shopbagData[i].isChecked){
          bool = true
          this.shopbagData.splice(i,1);
          i--;
        }
      }
      this.total = 0
      if(!bool){
        this.$toast("请选择删除的商品")
      }
    },
    // 计算商品总金额
    amount(){
       this.total = 0;
      this.shopbagData.map(v =>{
        if(v.isChecked){
          this.total += v.price * v.count;
        }
      })
        this.total *= 100;
    },
   
  //  通过计数器点击改变商品数量
    // counter(){
    //   this.amount()
    // },

      //判断是否勾选商品
    isSelectProduct(msg) {

      for (let i = 0; i < this.shopbagData.length; i++) {
        if (this.shopbagData[i].isChecked) {
          //如果存在勾选商品
          return true
        }
      }

      return false;
    },

    goShop(){
      this.$router.push({name:'Home'});
    },

  //  订单提交
    commit(){
  
   let isHas = this.isSelectProduct();
   if(!isHas){
     this.$toast("请选择购买的商品");
     return;
   }

  //  dids为勾选的商品的购物袋sid集合
  let sids = [];
  this.shopbagData.map(v =>{
    if(v.isChecked){
      sids.push(v.sid)
    }
  })
  // 跳转到结算页面且携带sids的查询参数
  this.$router.push({name:'Pay',query:{sids: sids.join('-')}})
      
    },
     //修改数量
    changeCount(item) {

      //sid: 购物袋sid
      //count: 商品数量

      //获取token
      let tokenString = localStorage.getItem("tokenString");
      // console.log('tokenString ==> ', tokenString);
      if (!tokenString) {
        //跳回登录页面
        this.$toast("请先登录");
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid: item.sid,
          count: item.count
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 700) {
            //token检验无效,则跳到登录页面
            this.$router.push({ name: "Login" });
          } else if (result.data.code == 6000) {
            if (item.isChecked) {
              this.amount();
            }
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });

      
    },


  },
};
</script>

